/* Chatウィンドウ全体のスタイル */
.container {
    width: auto;
    margin: 10 auto;
}

.chat-body {
    padding-left: 10px;
    padding-right: 10px;
    margin: 10px;
    margin-bottom: 100px;
}

.chat {
    margin-bottom: 10px;
}

.user-chat {
    display: flex;
    background-color: white;
    border-radius: 5px;
    align-items: center;
    color: blue;
}

.bot-chat {
    background-color: white;
    border-radius: 5px;
    align-items: center;
    color: green;
}

.bot-chat-message {
    display: flex;
    align-items: center;
}

.chat-image {
    width: 30px;
    height: 30px;
    margin-left: 10px;
    margin-right: 10px;
}

.chat-message {
    padding-left: 10px;
    padding-right: 10px;
}

.world-body {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}
  
.world-chat {
    position: relative;
    display: flex;
    flex: 0 0 240px;
    flex-direction: column;
    align-items: center;
    white-space: nowrap;
}
  
.main-image {
    border-radius: 5px;
    width: 220px;
    height: 150px;
    object-fit: cover;
}

.overlay-image {
    width: 50px;
    position: absolute;
    top: 2%;
    left: 72%;
    transition: transform 0.5s;
}

.world-name {
    width: 220px;
    margin: 0;
    margin-bottom: 10px;
    overflow: hidden;
    text-overflow: ellipsis;
    font-weight: bold;
  }

.chat-footer {
    background-color: white;
    border-radius: 5px;
    display: flex;
    position: fixed;
    align-items: center;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
}

.user-input {
    background-color: transparent;
    border: None;
    width: 800px;
    height: 20px;
    padding: 15px;
    outline: 0px;
}

.user-input:placeholder-shown + .send-button { 
    pointer-events: none;
}

.send-button {
    width: 30px;
    height: 30px;
    margin-left: 10px;
    margin-right: 10px;
}